/************** vertical timeline **************/
.timeline {
  position: relative;
  margin-bottom: 30px;
}
.timeline:before {
  content: "";
  position: absolute;
  width: 3px;
  top: 30px;
  left: 0;
  bottom: 0;
  background-color: var(--bs-border-color);
}
.timeline .timeline-item {
  display: flex;
}
.timeline .timeline-block {
  width: 100%;
}
.timeline .time-show-btn {
  margin-bottom: 30px;
}
.timeline .timeline-box {
  margin: 20px 0;
  position: relative;
  margin-left: 45px;
}
.timeline .timeline-date {
  display: inline-block;
  padding: 4px 16px 4px 8px;
  border-radius: 0px 30px 30px 0px;
  background-color: #04a2b3;
  color: #fff;
  position: relative;
  left: -30px;
}
.timeline .timeline-date .circle-dot {
  margin-right: 8px;
}
.timeline .timeline-icon {
  position: absolute;
  width: 10px;
  height: 10px;
  background: var(--bs-secondary-bg);
  border-radius: 50%;
  display: block;
  border: 2px solid #04a2b3;
  left: -48px;
  text-align: center;
  top: 27px;
  z-index: 9;
}
.timeline .timeline-icon:before {
  content: "";
  position: absolute;
  height: 3px;
  width: 20px;
  background-color: var(--bs-border-color);
  left: 12px;
  top: 3px;
}
.timeline .timeline-album {
  margin-top: 16px;
}
.timeline .timeline-album a {
  display: inline-block;
  margin-right: 5px;
}
.timeline .timeline-album img {
  height: 40px;
  width: auto;
  border-radius: 4px;
}

@media (min-width: 768px) {
  .timeline .timeline-block {
    width: 50%;
  }
  .timeline:before {
    left: 50%;
  }
  .timeline .time-show-btn {
    position: relative;
    left: 67px;
    text-align: right;
  }
  .timeline-item::before {
    content: "";
    display: block;
    width: 50%;
  }
  .timeline-item.timeline-left {
    text-align: right;
  }
  .timeline-item.timeline-left::after {
    content: "";
    display: block;
    width: 50%;
  }
  .timeline-item.timeline-left::before {
    display: none;
  }
  .timeline-item.timeline-left .timeline-box {
    margin-left: 0;
    margin-right: 45px;
  }
  .timeline-item.timeline-left .timeline-date {
    padding: 4px 8px 4px 16px;
    border-radius: 30px 0px 0px 30px;
    left: auto;
    right: -30px;
  }
  .timeline-item.timeline-left .timeline-date .circle-dot {
    float: right;
    margin-right: 0px;
    margin-left: 8px;
  }
  .timeline-item.timeline-left .timeline-icon {
    left: auto;
    right: -52px;
  }
  .timeline-item.timeline-left .timeline-icon::before {
    left: auto;
    right: 12px;
  }
}
.left-timeline::before {
  left: 3px;
}
.left-timeline .timeline-item::before {
  display: none;
}
.left-timeline .timeline-block {
  width: 100%;
}
.left-timeline .timeline-icon {
